<template>
  <div v-for="category in equipmentCommentList" :key="category.categoryCode">
    <v-card
      elevation="0"
      class="mx-0 px-0"
      v-if="!(hideEmpty && category.items.length == 0)"
    >
      <v-card-header>
        <v-card-header-text :id="getRefName(category.categoryCode, false)">
          <v-card-title
            class="text-subtitle-1"
            v-if="category.overrideCategoryName"
            ><v-icon aria-hidden="false" size="x-small">
              mdi-chevron-double-right </v-icon
            >{{ category.category }}</v-card-title
          >
          <v-card-title class="text-subtitle-1" v-else
            ><v-icon aria-hidden="false" size="x-small">
              mdi-chevron-double-right </v-icon
            >{{ category.category }}裝備
            <v-card-subtitle class="text-caption ms-2 align-self-end"
              >{{ category.categorySubTitle }} Equipment</v-card-subtitle
            >
          </v-card-title>
        </v-card-header-text>
      </v-card-header>
      <v-card-text>
        <EquipmentComment
          v-for="item in category.items"
          :key="item.id"
          :item="item"
          :categoryCode="category.categoryCode"
          :isCard="false"
        ></EquipmentComment>
      </v-card-text>
    </v-card>
    <v-card
      elevation="0"
      class="mx-0 px-0"
      v-if="category.hasCard && !(hideEmpty && category.cards.length == 0)"
    >
      <v-card-header>
        <v-card-header-text :id="getRefName(category.categoryCode, true)">
          <v-card-title class="text-subtitle-1"
            ><v-icon aria-hidden="false" size="x-small">
              mdi-chevron-double-right </v-icon
            >{{ category.category }}卡片
            <v-card-subtitle class="text-caption align-self-end ms-2"
              >{{ category.categorySubTitle }} Card</v-card-subtitle
            >
          </v-card-title>
        </v-card-header-text>
      </v-card-header>
      <v-card-text>
        <EquipmentComment
          v-for="card in category.cards"
          :key="card.id"
          :item="card"
          :categoryCode="category.categoryCode"
          :isCard="true"
        ></EquipmentComment>
        <v-alert
          v-if="category.hasCard && category.cards.length == 0"
          density="comfortable"
          type="info"
          variant="tonal"
        >
          {{ category.noCardMessage }}
        </v-alert>
      </v-card-text>
    </v-card>
  </div>
  <v-menu>
    <template v-slot:activator="{ props }">
      <v-btn
        size="large"
        class="btn-bookmark"
        icon="mdi-bookmark"
        color="primary"
        v-bind="props"
      >
      </v-btn>
    </template>

    <v-list class="btn-bookmark-menu" density="compact">
      <template v-for="(category, index) in equipmentCommentList" :key="index">
        <v-list-subheader
          >{{ category.category }}
          <span class="text-caption">{{
            category.categorySubTitle
          }}</span></v-list-subheader
        >
        <v-list-item-title :value="index" class="mb-2 ms-3">
          <v-btn
            v-if="category.items.length > 0"
            size="small"
            color="primary"
            class="mx-1"
            @click="navTo(category.categoryCode, false)"
            rounded="pill"
            >裝備</v-btn
          >
          <v-btn
            size="small"
            color="primary"
            class="mx-1"
            v-if="category.hasCard && category.cards.length > 0"
            @click="navTo(category.categoryCode, true)"
            rounded="pill"
            >卡片</v-btn
          >
        </v-list-item-title>

        <v-divider></v-divider>
      </template>
    </v-list>
  </v-menu>
</template>

<script>
import EquipmentComment from './EquipmentComment.vue';
export default {
  name: 'EquipmentCommentList',
  data: () => ({}),
  props: ['equipmentCommentList', 'hideEmpty'],
  components: {
    EquipmentComment,
  },
  methods: {
    navTo(categoryCode, isCard) {
      const refName = this.getRefName(categoryCode, isCard);
      //const el = this.$refs[refName][0];
      const el = document.getElementById(refName);
      console.log(el);
      this.$smoothScroll({
        scrollTo: el,
        duration: 300,
        offset: -50,
      });
    },
    getRefName(categoryCode, isCard) {
      let target = '';
      if (!isCard) {
        target = `${categoryCode}-equipment`;
      } else {
        target = `${categoryCode}-card`;
      }
      return target.replace(/-/gi, '_');
    },
  },
};
</script>
<style scope>
.btn-info {
  min-width: 50px !important;
  width: 50px !important;
}
.btn-info .icon {
  padding-bottom: 1px !important;
}
.rank {
  padding-top: 1px;
}
.btn-bookmark {
  position: fixed;
  bottom: 19px;
  right: 85px;
  z-index: 99;
  padding: 0;
  opacity: 70%;
}
.btn-bookmark-menu {
  right: 150px;
}
@media (max-width: 500px) {
  .btn-bookmark {
    bottom: 2px;
    right: 60px;
  }
}
</style>
