<template>
  <div class="my-5 mx-3 text-h5 font-weight-medium">
    {{ getNewEquipments.title }}
  </div>
  <v-divider></v-divider>
  <EquipmentCommentList
    :equipmentCommentList="getNewEquipments.comments"
    :hideEmpty="true"
  />
</template>
<script>
import { mapActions, mapGetters } from 'vuex';
import EquipmentCommentList from '@/components/EquipmentCommentList.vue';
export default {
  name: 'NewEquipmentView',
  components: { EquipmentCommentList },
  data: () => ({}),
  props: [],
  computed: { ...mapGetters('misc', ['getNewEquipments']) },
  async created() {},
  async mounted() {
    await this.fetchNewEquipments();
    this.$nextTick(function () {
      this.updateLoadStatus(true);
    });
  },
  methods: {
    ...mapActions(['updateLoadStatus']),
    ...mapActions('misc', ['fetchNewEquipments']),
  },
};
</script>
