<template>
  <div class="">
    <v-btn class="btn-info" color="primary" size="x-small" variant="outlined">
      <v-icon class="ma-0 icon" aria-hidden="false"> mdi-information </v-icon>
      INFO

      <v-dialog v-model="dialog" activator="parent">
        <v-card>
          <v-card-header-text>
            <v-card-title
              >{{ item.name }} {{ getSlotText(item.slots) }}</v-card-title
            >
          </v-card-header-text>
          <v-divider></v-divider>
          <template v-for="(info, index) in item.infos" :key="index">
            <v-card elevation="0">
              <v-card-text>
                <template v-for="(line, index) in info.contents" :key="index">
                  <div v-html="line" v-if="line.length > 0"></div>
                  <br v-else />
                </template>
                <br />
                <div>
                  <div>
                    <span v-if="!isSpecialCategory"
                      >系列：{{ info.class }}
                    </span>
                    <span class="ms-5" v-if="!isSpecialCategory && !isCard">
                      {{ getValueTitle(categoryCode) }}：{{ info.value }}
                    </span>
                  </div>
                  <div v-if="!isSpecialCategory">
                    <span v-if="info.position.length > 0"
                      >位置：{{ info.position }}
                    </span>
                    <span :class="info.position.length > 0 ? 'ms-5' : ''"
                      >重量：{{ info.weight }}</span
                    >
                  </div>
                  <div v-if="!isSpecialCategory && !isCard">
                    <span>要求等級：{{ info.requiresLevel }} </span>
                  </div>
                  <div v-if="!isSpecialCategory && !isCard">
                    <span>裝備等級：{{ info.equipmentLevel }} </span>
                  </div>
                  <div v-if="!isSpecialCategory && !isCard">
                    <span>裝備：{{ info.usableBy }} </span>
                  </div>
                </div>
                <template v-if="!isSpecialCategory && !isCard">
                  <v-divider
                    v-if="info.enchantments.length > 0"
                    class="my-3"
                  ></v-divider>
                  <ul class="main-list ms-4 text-primary">
                    <li
                      v-for="(enchantment, index) in info.enchantments"
                      :key="index"
                    >
                      {{ enchantment.name }}
                      <ul class="sub-list ms-6">
                        <li
                          v-for="(detail, indexDetail) in enchantment.detail"
                          :key="indexDetail"
                        >
                          {{ detail }}
                        </li>
                      </ul>
                    </li>
                  </ul>
                </template>
              </v-card-text>
            </v-card>
          </template>

          <v-card-actions class="d-flex justify-end">
            <v-btn block size="small" color="primary" @click="dialog = false">
              關閉
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-btn>
  </div>
</template>
<script>
export default {
  name: 'EquipmentInfo',
  data: () => ({
    dialog: false,
  }),
  props: ['item', 'categoryCode', 'isCard'],
  computed: {
    isSpecialCategory() {
      return this.categoryCode == 'costume' || this.categoryCode == 'pet';
    },
  },
  methods: {
    getSlotText(slots) {
      let text = '';
      if (slots > 0) {
        text = `[${slots}]`;
      }
      return text;
    },
    getValueTitle(categoryCode) {
      let title = '';
      switch (categoryCode) {
        case 'weapon':
          title = '攻擊';
          break;

        default:
          title = '防禦';
          break;
      }
      return title;
    },
  },
};
</script>
<style scoped>
.btn-info {
  min-width: 50px !important;
  width: 50px !important;
}
.btn-info .icon {
  padding-bottom: 0px !important;
}
.main-list > li {
  color: #0d6efd;
}

.sub-list > li {
  color: #2c3e50;
}
</style>
